.footer_3_pagePart{font-size:1rem;display:flex;flex-direction:column;justify-content:center;align-items:center;background:var(--first-background-color, black)}.footer_3_pagePart .width_wrapper{display:flex;flex-direction:row;justify-content:space-between;flex-wrap:wrap;gap:50px;padding:100px 0}@media screen and (width <= 500px){.footer_3_pagePart .width_wrapper{gap:30px;padding:30px 0}}.footer_3_pagePart .logoSection{display:flex;flex-direction:column;gap:20px;flex:1 1 300px}.footer_3_pagePart .logoSection .logoContainer{width:150px}.footer_3_pagePart .logoSection .social_content{display:flex;flex-direction:row;gap:10px}.footer_3_pagePart .logoSection .social_content .icon{width:1.5em}.footer_3_pagePart .menuContainer{display:flex;flex-direction:row;justify-content:flex-end;flex-wrap:wrap;flex:1 1 auto;gap:30px}@media screen and (width <= 820px){.footer_3_pagePart .menuContainer{justify-content:space-between}}@media screen and (width <= 500px){.footer_3_pagePart .menuContainer{display:flex;flex-direction:column;align-items:flex-start}}.footer_3_pagePart .menuContainer .menuHeading{font-size:1rem;font-weight:600;color:var(--first-text-color, white)}.footer_3_pagePart .menuContainer .menu{display:flex;flex-direction:column;gap:10px}.footer_3_pagePart .menuContainer .menuList{display:flex;flex-direction:column;gap:4px}.footer_3_pagePart .menuContainer .menuLink{color:var(--second-text-color, black);font-size:.9em;white-space:nowrap}.footer_3_pagePart .sub_footer{display:flex;justify-content:center;align-items:center;width:100%;padding:20px 10px;border-top:1px solid var(--first-border-color, gray);background:var(--first-background-color, black);text-align:center}.footer_3_pagePart .sub_footer .width_wrapper_2{display:flex;flex-direction:row;justify-content:space-between;gap:20px;width:90%;color:var(--second-text-color, black);font-size:.8em}.footer_3_pagePart .sub_footer .link{color:var(--second-text-color, #f5f5f5)}.footer_3_pagePart .sub_footer .tac{display:flex;flex-direction:row;gap:10px}